import {
  Ref,
  memo,
  useRef,
  useState,
  useMemo,
  useEffect,
  forwardRef,
  useCallback,
  useImperativeHandle,
} from 'react';
import L from 'leaflet';
import dynamic from 'next/dynamic';
import { useSelector } from 'react-redux';
import { MapContainer, TileLayer, useMap } from 'react-leaflet';
import { I_MapConfig, I_Map, I_Scene } from '../../types';
import { DEFAULT_VALUE_MAP_CONFIG } from '@containers/Home/constants';
import { selectListScene } from '../../selectors';
import { GoogleMapWrapper } from './styled';
import 'leaflet-defaulticon-compatibility';

const MAP_TILES = {
  roadmap: 'https://mt1.google.com/vt/lyrs=m&x={x}&y={y}&z={z}',
  satellite: 'https://mt1.google.com/vt/lyrs=s&x={x}&y={y}&z={z}',
  hybrid: 'https://mt1.google.com/vt/lyrs=y&x={x}&y={y}&z={z}',
  terrain: 'https://mt1.google.com/vt/lyrs=p&x={x}&y={y}&z={z}',
};

const Marker = dynamic(() => import('./Marker'), { ssr: false });

export type Type_RefFuncGoogleMap = {
  onFitBounds: () => void;
  onInvalidateSize: () => void;
  onPanToMarker: (marker: I_Map['markers'][number]) => void;
};

interface Props {
  mapData?: I_Map;
  sceneSelected?: I_Scene;
  configMap?: I_MapConfig;
  onChangeScene: (scene: I_Scene) => void;
  markerSelected?: I_Map['markers'][number];
}

const MapController = memo(
  ({
    isMapLoaded,
    onFitBounds,
    onPanToMarker,
    markerSelected,
    setIsMapLoaded,
  }: {
    onFitBounds: () => void;
    onPanToMarker: (marker: any) => void;
    markerSelected?: any;
    isMapLoaded: boolean;
    setIsMapLoaded: (val: boolean) => void;
  }) => {
    const map = useMap();

    useEffect(() => {
      if (map) setIsMapLoaded(true);
    }, [map, setIsMapLoaded]);

    useEffect(() => {
      if (isMapLoaded) {
        if (markerSelected) onPanToMarker(markerSelected);
        else onFitBounds();
      }
    }, [isMapLoaded, markerSelected, onFitBounds, onPanToMarker]);

    return null;
  },
);

const Map = forwardRef(
  (
    { configMap, sceneSelected, mapData, markerSelected, onChangeScene }: Props,
    ref: Ref<Type_RefFuncGoogleMap>,
  ) => {
    const mapRef = useRef<L.Map | null>(null);

    const [isMapLoaded, setIsMapLoaded] = useState(false);

    const listScene = useSelector(selectListScene);

    const configRadar = useMemo(
      () => ({
        ...DEFAULT_VALUE_MAP_CONFIG.radar,
        ...configMap?.radar,
      }),
      [configMap?.radar],
    );
    const configMarker = useMemo(
      () => ({
        ...DEFAULT_VALUE_MAP_CONFIG.marker,
        ...configMap?.marker,
      }),
      [configMap?.marker],
    );

    const tileUrl = useMemo(() => {
      const type = configMap?.mapType;
      if (type && MAP_TILES[type]) return MAP_TILES[type];
      return 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png';
    }, [configMap?.mapType]);

    const onFitBounds = useCallback(() => {
      if (mapRef.current && mapData?.markers?.length) {
        const bounds = L.latLngBounds(
          mapData.markers.map(m => [m.lat, m.lng] as L.LatLngTuple),
        );
        mapRef.current.fitBounds(bounds, { padding: [50, 50] });
      }
    }, [mapData]);

    const onPanToMarker = useCallback((marker: typeof markerSelected) => {
      if (mapRef.current && marker)
        mapRef.current.setView([marker.lat, marker.lng], 15);
    }, []);

    const onClickMarker = useCallback(
      marker => {
        if (marker?.target_scene_id) {
          const scene = listScene.find(
            item => item.id === marker.target_scene_id,
          );
          if (scene) onChangeScene(scene);
        }
      },
      [listScene, onChangeScene],
    );

    useImperativeHandle(
      ref,
      () => ({
        onFitBounds,
        onPanToMarker,
        onInvalidateSize: () => mapRef.current?.invalidateSize(),
      }),
      [onFitBounds, onPanToMarker],
    );

    return (
      <GoogleMapWrapper>
        <MapContainer
          ref={mapRef}
          zoom={12}
          minZoom={5}
          center={[0, 0]}
          zoomControl={false}
          scrollWheelZoom={true}
          attributionControl={false}
          style={{ width: '100%', height: '100%' }}
        >
          <TileLayer url={tileUrl} />
          <MapController
            isMapLoaded={isMapLoaded}
            onFitBounds={onFitBounds}
            onPanToMarker={onPanToMarker}
            markerSelected={markerSelected}
            setIsMapLoaded={setIsMapLoaded}
          />
          {mapData?.markers.map(item => (
            <Marker
              key={item.id}
              lat={item.lat}
              lng={item.lng}
              marker={item}
              configRadar={configRadar}
              configMarker={configMarker}
              onClick={() => onClickMarker(item)}
              isTurnOnRadar={!!configRadar.isEnabled}
              isActive={markerSelected?.id === item.id}
              northRadar={sceneSelected?.north_ath || 0}
            />
          ))}
        </MapContainer>
      </GoogleMapWrapper>
    );
  },
);

export default memo(Map);
